import {GetDataList} from '@/api/class-center'
export default {
	props:{
		askclassid:Number,
		selectid:Number
	},
	data(){
		return{
			askList:[],//问答列表
			getParameters:{
				a:'getplaytime',
				playtime:'ask',//小组‘group’，问答‘ask’，作品‘works’，资讯‘information’
				pageindex:1,
				pagesize:10,
				key:'',
				askclassid:0,//问答的分类id
				selectid:0,//问答的筛选类型id
			},
			pageCount:0,
			refreshStatus:false,
			height:0
		}
	},
	watch:{
		'askclassid':{
			handler(askclassid){
				this.getParameters.askclassid = this.askclassid
				this.loadPage()
			},
			
			deep:true
		},
		'selectid':{
			handler(selectid){
				this.getParameters.selectid = this.selectid
				this.loadPage()
			},
			
			deep:true
		},
	},
	mounted(){
		if(this.isrefresh){
			//console.log(111)
			this.loadPage()
		}
		uni.showLoading({
		    title: '加载中'
		});
		uni.getSystemInfo({
			success:res=>{
				this.height= res.windowHeight-88
			}
		})
		this.getParameters.askclassid = this.askclassid
		this.getParameters.selectid = this.selectid
		this.loadPage()
	},
	methods: {
		onRefresh(){
			this.loadPage()
			// setTimeout(()=>{
			// 	this.refreshStatus = false
			// },2000)
		},
		openUrl(url){
			uni.navigateTo({
			    url:url
			});
		},
		scrolltolower(){
			this.getParameters.pageindex++
			if(this.getParameters.pageindex <= this.pageCount){
				this.loadPage('more')
			}
		},
		//初始数据的请求
		loadPage(status){
			if(this.loading){
				return
			}
			this.loading = true
			if(status=='more'){
				GetDataList(this.getParameters).then(res=>{
					if(res.result){
						this.askList = this.askList.concat(res.askmodel.data)
					}
					this.loading = false
				})
			}else{
				this.getParameters.pageindex=1
				GetDataList(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.askmodel.pageCount
						this.askList = res.askmodel.data
					}else{
						this.askList = []
						this.pageCount = 0
					}
					uni.hideLoading();
					this.loading = false
					setTimeout(()=>{
						this.refreshStatus = false
					},1000)
					
				})
			}
		}
		
	}
}
